.gif_card{
      width: 158px;
      height: 22rem;
      padding: 2rem;
      background: var(--light);
      display: flex;
      flex-direction: column;
      align-items: center;
      margin-bottom: 3rem;
}

.gif_img{
      width: 110px;
      height: 110px;
      background: var(--light);
      border: solid .1rem var(--tertiary);
      display: flex;
      align-items: center;
      justify-content: center;
      margin: .5rem;
      flex-shrink: 0;
}

.gif_img__box{
      width: 80px;
      height: 80px;
}

@keyframes loop_gif{
      100%{
            background-position: right top;
      }
}

.gif_model{
      width: 100%;
      min-height: 3.6rem;
      margin-top: 2.5rem;
      background: var(--gradient-45);
      display: flex;
      align-items: center;
      justify-content: center;
      border-radius: .3rem;
}

.gif_model strong{
      text-transform: uppercase;
      font-size: 1.2rem;
      font-weight: 500;
      color: var(--light);
}